.product {
	display: flex !important;
	flex-direction: column;
	letter-spacing: 0.03em;
	position: relative;
	margin: 10px !important;
	height: unset;
	max-width: 550px;
	max-height: 670px;
	&.new {
		.new {
			display: flex; } }
	&.discount {
		.discount {
			display: flex; }
		.product-price {
			&__curr {
				color: $green;
				font-style: normal;
				font-weight: 600;
				font-size: 30px;
				&::after {
					color: $green;
					font-style: normal;
					font-weight: 600;
					font-size: 30px; } }
			&__old {
				display: inline-block; } } }

	.new,
	.discount {
		margin: 0 10px;
		width: 48px;
		height: 48px;
		border-radius: 100%;
		text-align: center;
		@include flex(row, center, center, wrap);
		color: white;
		font-weight: 700;
		font-size: 15px;
		background: $red;
		line-height: 1;
		display: none; }
	.new {
		margin: 0;


		&__box {
			margin: 0 0 0 auto; } }

	&-labels {
		padding: 10px;
		@include flex(row, between, start, wrap);
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;
		width: 100%; }
	&-img {
		@include flex(row, center, center, nowrap);
		background: white;
		border: 1px solid $gray_light2;
		height: 408px;
		width: 100%;
		@include mq($max-width: 700px) {
			height: 300px; } }
	&__img {
		display: block;
		width: 90%;
		height: auto;
		max-height: 90%;
		object-fit: contain; }
	&__articul {
		margin: 14px 0;
		text-align: right;
		font-weight: 500;
		font-size: 14px;
		letter-spacing: 0.03em;
		color: $gray;
		@include mq($max-width: 575px) {
			font-size: 12px; } }
	&__title {
		flex: 1;

		font-style: normal;
		font-weight: bold;
		font-size: 16px;
		line-height: 20px;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		color: #686868;
		padding: 0 10px 0 0;
		@include mq($max-width: 1500px) {
			font-size: 16px; }
		@include mq($max-width: 575px) {
			font-size: 14px;
			line-height: 1; } }
	&-price {
		margin: 40px 0 20px;
		text-align: left;
		font-size: 30px;
		font-weight: 600;

		display: flex;
		align-items: center;
		@include mq($max-width: 575px) {
			font-size: 16px;
			margin: 10px 0; }
		&__curr {
			display: inline-block;
			color: $dark;
