			// 	+mq($max-width: 575px)
 }			// 		font-size: 14px
		&__old {
			display: none;
			color: $price_old;
			font-style: normal;
			font-weight: normal;
			font-size: 23px;
			line-height: normal;
			margin-left: 10px;
			text-align: center;
			letter-spacing: 0.03em;
			text-decoration-line: line-through;
			position: relative;
