.choose {
	margin-left: 5px;
	background: none;
	cursor: pointer;
	svg {
		width: 20px; }

	&.active,
	&:hover {
		svg {
			path {
				fill: $red; } } }

	&_compare {

		&_add {
			svg {
				path {
					fill: $red; } } } }

	&_love {
		svg {
			path {
				@include trs;
				fill: transparent;
				stroke: $red;
				stroke-width: 55; } }

		&_add {
			svg {
				path {
					@include trs;
					fill: $red;
					stroke: $red;
					stroke-width: 55; } } } } }
