.title {
    width: 100%;
    flex: 1;
    h1 {
        font-family: 'Mandy', serif;
        font-size: 35px;
        color: black;
        margin: 0 0 36px 0;
        @include mq($max-width: 1200px) {
            margin: 0 0 20px 0; }
        @include mq($max-width: 575px) {
            font-size: 24px; } }
    h2 {
        font-family: 'Mandy', serif;
        font-size: 35px;
        color: black;
        margin: 0 0 36px 0;
        @include mq($max-width: 1200px) {
            margin: 0 0 20px 0; }
        @include mq($max-width: 575px) {
            font-size: 24px; } }

    &_pink {
        h1,h2 {
            color: $pink; } }
    &_green {
        h1,h2 {
            color: $green; } }
    &_center {
        h1,h2 {
            text-align: center; } }

    &_form {
        font-family: 'Mandy', serif;
        font-size: 30px;
        color: $green;
        margin: 0 0 10px 0;
        text-align: center;
        @include mq($max-width: 1200px) {
            margin: 0 0 20px 0; }
        @include mq($max-width: 575px) {
            font-size: 24px; } }

    &__inner {
        width: 100%;
        padding: 10px 0 0 0;
        margin: 0 auto;
        max-width: 1560px;
        @include mq($max-width: 1650px) {
            padding: 0 15px;
            max-width: 100%; }
        @include mq($max-width: 1200px) {
            padding: 0 15px;
            max-width: 100%; }
        @include mq($max-width: 575px) {
            padding: 0 10px; } }

    &_subtitle {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 21px;
        text-align: center;

        color: #000000;
        margin-bottom: 30px;

        a {
            color: $green;
            font-weight: bold; } }

    &_link {
        color: black;
        font-weight: normal;
        text-decoration: underline; } }

