.breadcrumbs {
    width: 100%;
    height: 65px;
    display: flex;
    align-items: center;
    @include mq($max-width: 575px) {
        min-height: 50px;
        padding: 10px 0;
        height: unset; }

    &__item {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        color: $pink; }
    &__link {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        color: $pink;

        &::after {
            content: '';
            display: inline-block;
            margin: 0 7px;
            height: 8px;
            width: 8px;
            background-color: $pink;
            border-radius: 50%; } }


    &__inner {
        display: flex;
        flex-flow: wrap;

        max-width: 1560px;
        width: 100%;
        padding: 0;
        margin: 0 auto;
        @include mq($max-width: 1650px) {
            padding: 0 15px;
            max-width: 100%; }
        @include mq($max-width: 1200px) {
            padding: 0 15px;
            max-width: 100%; }
        @include mq($max-width: 575px) {
            padding: 0 10px; } } }
