.foto {

    width: 100%;

    @include mq($max-width: 1024px) {

        margin: 0 auto;
        padding: 0 0 20px 0; }
    @include mq($max-width: 575px) {
        margin: 0; }
    &__div {
        width: 100%;
        overflow: hidden; }

    &__pictures {
        grid-area: image;
        position: relative;
        align-self: start;

        @include mq($max-width: 768px); }

    &__zoom {
        position: absolute;
        right: 0;
        top: 0;
        height: 75px;
        width: 75px;
        background: rgba(72, 72, 72, 0.92);
        z-index: 10;
        display: flex;
        align-items: center;
        justify-content: center;
        pointer-events: none;
        cursor: pointer; }

    &__images {

        @include mq($max-width: 768px);

        img {
            height: 100%;
            width: 100%;
            object-fit: cover; }

        .slick-track {
            display: flex;
            align-self: stretch;
            height: 100%; } }

    &__item {
        height: 300px; }



    &__slider {
        padding: 0 30px;
        img {
            margin: 20px 5px;

            cursor: pointer;
            height: 116px;
            object-fit: cover; }

        .slick-current {

            opacity: 1; }

        .slick-list {
            // margin-right: 20px
            // margin-left: 20px
            margin-left: 10px;
            margin-right: 10px; } }

    &__controls {
        position: absolute;
        bottom: 92px;
        left: 0;
        width: 100%; }


    &__arrows {
        display: flex;
        width: 100%;
        height: 0;
        justify-content: space-between; }

    &__arrow {
        width: 43px;
        height: 43px;
        cursor: pointer;

        &_next {
            margin-right: -10px; }
        &_prev {
            transform: scaleX(-1);
            margin-left: -10px; } }

    .slick-disabled {
        cursor: default;
        svg {
            path {
                fill: #AEAEAE; } } }

    &__video {

        display: flex !important;
        align-items: center;
        justify-content: center;

        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;

        img {
            width: 100%;
            height: 100%; }

        &_mini {
            margin: 20px 5px;
            display: flex !important;
            cursor: pointer;
            height: 116px;

            img {
                margin: 0; }

            .foto__play {
                height: 80px;
                width: 80px; } } } }
