@keyframes footprintAnimation {
    0% {
        opacity: 1; }

    50% {
        opacity: 0; }

    100% {
        opacity: 0; } }

@keyframes path1 {
    0% {
        transform: rotate(0deg) scaleX(-1) translate3d(0%, -50%, 0); }

    30% {
        transform: rotate(20deg) scaleX(-1) translate3d(-35%, -60%, 0); }

    100% {
        transform: rotate(20deg) scaleX(-1) translate3d(0%, -80%, 0); } }
