.count {
    min-width: 17px;
    height: 17px;
    @include flex(row, center, center, nowrap);
    position: absolute;
    right: -6px;
    bottom: 0;
    z-index: 2;
    border-radius: 8.5px;
    background: white;
    box-shadow: 0 0 0 2px white;
    color: $green;
    font-weight: 900;
    font-size: 10px;
    line-height: 1;
    padding: 0 3px;
    border: 2.5px solid $green;
    letter-spacing: 0.03em; }
